import React, { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { NavLink } from 'react-router-dom';
import { demoAction, getApiPhimAction } from '../../redux/actions/PhimActions';
export default function HomeMoBile(props) {

    console.log('propsHome', props);
    //props.history: gip iu hng url


    //Ly mng phim t redux v
    let { mangPhim } = useSelector(rootReducer => rootReducer.PhimReducer);

    const dispatch = useDispatch();

    useEffect(() => {
        //D gi hm hay khng gi hm th action api cng phi l function (dispatch) {}
        const action = getApiPhimAction('GP02');
        dispatch(action);

    }, [])

    const renderPhim = () => {
        return mangPhim.map((phim, index) => {
            return <div className="row mt-2" key={index}>
                <div className="col-3">
                    <img className='w-100' height={100} onError={(e) => { e.target.src = `https://i.pravatar.cc/300?u=${phim.tenPhim}` }} src={phim.hinhAnh} alt="..." />
                </div>
                <div className="col-9">
                    <p>{phim.tenPhim}</p>
                    <div className='text-right'>

                        <p style={{ height: 75 }}>{phim.moTa.length > 100 ? phim.moTa.substr(0, 100) + '...' : phim.moTa}</p>
                        <NavLink to={`/detail/${phim.maPhim}`} className="btn btn-danger">MUA V</NavLink>
                        <br />
                        <button className="btn btn-outline-danger " onClick={() => {
                            props.history.push('/detail/' + phim.maPhim);
                        }}>MUA V</button>
                    </div>
                </div>

            </div>
        })
    }
    return (
        <div className="container">
            <button className="btn btn-success" onClick={() => {
                //Chuyn hng trang v trang ch (props.history.push() dng  chuyn hng trang trong 1 s kin x l khc vi NavLink)
                props.history.push('/');
                //Chuyn hng v trang trc  
                props.history.goBack();
            }}>Go home</button>
            <h3>Danh sch phim</h3>
            <div>
                {renderPhim()}
            </div>
        </div>
    )

}
