import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import { Route } from 'react-router-dom'
import HeaderHome from '../components/HeaderHome/HeaderHome';


export const HomeTemplate = (props) => {

    const [widthHeight, setWidthHeight] = useState({
        width: window.innerWidth, // phn gii mn hnh mc nh
        height: window.innerHeight //
    });

    useEffect(() => {

        //Khai bo s kin thay i kch thc mn hnh
        window.onresize = () => {
            //Set li  phn gii sau khi co gin mn hnh
            setWidthHeight({
                width: window.innerWidth, // phn gii mn hnh mc nh
                height: window.innerHeight //
            })
        }
        window.onload = () => {
            //Set li  phn gii sau khi co gin mn hnh
            setWidthHeight({
                width: window.innerWidth, // phn gii mn hnh mc nh
                height: window.innerHeight //
            })
        }
        return () => {
            //Hu ng k s kin khi chuyn qua template khc
            window.removeEventListener('onresize');
            window.removeEventListener('onload');
        }
    }, [])

    let { Component, path } = props;

    const renderComponent = (propsRoute) => {
        //Nu c component Mobile v width nh hn 765 th mi render ra component mobile
        if (widthHeight.width < 765 && props.MobileComponent) {
            return <props.MobileComponent {...propsRoute} />
        }

        return <Component {...propsRoute} />
    }
    return <Route exact path={path} render={(propsRoute) => {

        return <div>
            <HeaderHome />
            {renderComponent(propsRoute)}
        </div>
    }} />
}