

const staeDefault = {
    arrContent: [
        { name: 'Thanh', content: 'Hello Huy' },
        { name: 'Si', content: 'Hello Huy p trai' }
    ]
}

export const FaceBookAppReducer = (state = staeDefault, action) => {

    switch (action.type) {
        case 'ADD_CONTENT': {
            //Va sao chp ra mng mi va push 1 phn t action.content vo mng
            state.arrContent = [...state.arrContent, action.content];

            return { ...state }
        }
        default: return { ...state }
    }
}