import React, { useEffect, useState } from 'react'

export default function DemoUseEffect() {

    let [number, setNumber] = useState(1);
    let [like, setLike] = useState(1);

    useEffect(() => {
        //Chy 1 ln duy nht sau khi component render xong
        console.log('Thay th componentDidMount')
    }, [])

    useEffect(() => {
        console.log('Khi no number thay i th hm ny mi chy - tng ng componentDidUpdate')
    }, [number])

    useEffect(() => {

        return () => {
            console.log('Hm ny s c thc thi khi component mt khi giao din ging componenWillUnmount')
        }
    }, [])

    console.log('render');

    return (
        <div>
            Demo UseEffect
            <hr />
            <h3>Number: {number}</h3>
            <h3>Like:{like}</h3>
            <button className='btn btn-outline-danger mt-2' onClick={() => {
                setNumber(number + 1);
            }}>+</button>
            <button className='btn btn-outline-danger ml-2' onClick={() => {
                setLike(like + 1);
            }}>+ Like</button>
        </div>
    )
}
