//Gi tr mc nh ban u
const stateDefault = []

export const gioHangReducer = (state = stateDefault, action) => {

    switch (action.type) {
        case 'THEM_GIO_HANG': {
            let gioHang = state;
            //Kim tra sp c trong gi hng cha
            let spGH = gioHang.find(sp => sp.maSP === action.spGH.maSP);
            if (spGH) {
                spGH.soLuong += 1;
            } else {
                gioHang.push(action.spGH);
            }
            //Tr v state mi => giao din mi cp nht
            //Immutable: tnh cht bt bin ca redux
            console.log('gioHang', gioHang)
            return [...gioHang]; //Tr v state sau khi x l
        }
        case 'XOA_GIO_HANG': {
            let gioHangCapNhat = state.filter(sp => sp.maSP !== action.maSP);
            return [...gioHangCapNhat]
        }
        case 'TANG_GIAM_SO_LUONG': {
            let gioHangCapNhat = [...state];

            let spGH = gioHangCapNhat.find(sp => sp.maSP === action.maSP);
            if (spGH) {
                spGH.soLuong += action.soLuong
                if (spGH.soLuong < 1) {
                    spGH.soLuong -= action.soLuong;
                    alert('S lng ti thiu l 1')
                }
            }
        }
            return [...state]
    }

    return state;
}